label {
    display: block;
    margin-top: 30px;
    font-size: 16px;
    font-weight: 500;
}

.userInfo {
    display: flex;
}

.text {
    display: block;
    height: 50px;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.07);
    border-radius: 3px;
    padding: 0 10px;
    margin-top: 8px;
    font-size: 14px;
    font-weight: 300;
}

::placeholder {
    color: #e5e5e5;
}

.register {
    text-align: center;
    width: 100%;
    background-color: #ffffff;
    color: #080710;
    padding: 15px 0;
    font-size: 18px;
    font-weight: 600;
    border-radius: 5px;
    cursor: pointer;
    text-decoration: none;
}

.registration {
    box-sizing: border-box;
    width: 450px;
    background-color: #4a4c93a6;
    position: relative;
    border-radius: 10px;
    backdrop-filter: blur(10px);
    border: 2px solid rgba(255, 255, 255, 0.1);
    box-shadow: 0 0 40px rgba(8, 7, 16, 0.6);
    padding: 35px;
}

.registration * {
    font-family: 'Poppins', sans-serif;
    color: #ffffff;
    letter-spacing: 0.5px;
    outline: none;
    border: none;
}

.registration h3 {
    font-size: 32px;
    font-weight: 500;
    line-height: 42px;
    text-align: center;
}

.btnreg {
    margin-top: 25px;
    width: 100%;
    background-color: #ffffff;
    color: #080710;
    padding: 15px 0;
    font-size: 18px;
    font-weight: 600;
    border-radius: 5px;
    cursor: pointer;
}

.authContainer {
    display: flex;
    justify-content: center;
    padding-top: 50px;
    padding-bottom: 100px;
}

.ErrorMessage {
    color: #ba3939;
    margin-top: 5px
}

.avaContainer img:hover {
    transform: scale(1.4);
    cursor: pointer;
}


.avacontainer {
    display: flex;
    /* flex-flow: column; */
}

@media only screen and (max-width: 500px) {
    .registration {
        width: 350px;
    }

    .btnreg {

        padding: 10px 0;

    }
}